<template>
<div>
  <div class="steps-titles">
    针对部分类型数据提供的快速配置，将引导您完成简单的业务设置。您也可以直接编辑过滤语句，实现更复杂的查询要求。
  </div>
  <div class="steps-info">
    <div class="steps-border">
      <p>基本信息</p>
      <a-form-model
        ref="ruleForm"
        :model="form"
        :rules="rules"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-form-model-item ref="ruleLabel" label="规则名称" prop="ruleLabel">
          <a-input v-model="form.ruleLabel" placeholder="请输入规则名称"/>
        </a-form-model-item>
        <a-form-model-item ref="description" label="规则描述">
          <BTextarea
            class="full-width"
            style="height: auto"
            v-model="form.description"
            placeholder="请输入描述"
          />
        </a-form-model-item>
        <a-form-model-item label="数据来源" prop="subjectResource">
          <a-select v-model="form.subjectResource" placeholder="请选择数据来源" @change="changeRes">
            <a-select-option v-for="(item, index) in resourceArr" :value="item.value" :key="index">
              {{item.label}}
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="触发事件" prop="subjectEvent">
          <a-select v-model="form.subjectEvent" placeholder="请选择触发事件">
            <a-select-option v-for="(item,index) in eventArr" :value="item.value" :key="index">
              {{item.label}}
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item :wrapper-col="{ span: 14, offset: 4 }">
          <a-button type="primary" @click="onSubmit">
            创建规则
          </a-button>
        </a-form-model-item>
      </a-form-model>
    </div>
  </div>
</div>
</template>

<script>
import BTextarea from '../../../components/BTextarea/BTextarea'
export default {
  name: 'stepsOne',
  components: {
    BTextarea
  },
  data() {
    return {
      eventArr: [],
      resourceArr: [
        {
          value: 'DEVICE',
          label: '设备'
        },
        {
          value: 'DEVICE_MESSAGE',
          label: '设备消息'
        },
        {
          value: 'DEVICE_PROPERTY',
          label: '设备属性'
        },
        {
          value: 'DEVICE_STATUS',
          label: '设备状态'
        },
        {
          value: 'PRODUCT',
          label: '产品'
        }
      ],
      labelCol: {span: 4},
      wrapperCol: {span: 20},
      other: '',
      form: {
        ruleLabel: '',
        description: '',
        subjectEvent: undefined,
        subjectResource: undefined
      },
      rules: {
        ruleLabel: [
          { required: true, message: '请输入规则名称', trigger: 'blur' }
        ],
        subjectEvent: [{ required: true, message: '请选择触发事件', trigger: 'change' }],
        subjectResource: [{ required: true, message: '请选择数据来源', trigger: 'change' }],
        description: [
          { validator: this.$validator.checkRemarkLength, message: '描述最多128字符', trigger: ['blur', 'change'] }
        ]
      },
      ruleId: ''
    }
  },
  methods: {
    changeRes(val) {
      switch (val) {
        case 'DEVICE':
          const devArr = [
            {
              value: 'DEVICE_CREATE',
              label: '设备创建'
            },
            {
              value: 'DEVICE_UPDATE',
              label: '设备更新'
            },
            {
              value: 'DEVICE_DELETE',
              label: '设备删除'
            }
          ]
          this.eventArr = devArr
          break
        case 'DEVICE_MESSAGE':
          const messArr = [
            {
              value: 'DEVICE_MESSAGE_REPORT',
              label: '设备消息上报'
            }
          ]
          this.eventArr = messArr
          break
        case 'DEVICE_PROPERTY':
          const meArr = [
            {
              value: 'DEVICE_PROPERTY_REPORT',
              label: '设备属性上报'
            }
          ]
          this.eventArr = meArr
          break
        case 'DEVICE_STATUS':
          const stetusArr = [
            {
              value: 'DEVICE_STATUS_UPDATE',
              label: '设备状态更改'
            }
          ]
          this.eventArr = stetusArr
          break
        case 'PRODUCT':
          const productArr = [
            {
              value: 'PRODUCT_CREATE',
              label: '产品创建'
            },
            {
              value: 'PRODUCT_UPDATE',
              label: '产品更新'
            },
            {
              value: 'PRODUCT_DELETE',
              label: '产品删除'
            }
          ]
          this.eventArr = productArr
          break
      }
    },
    onSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.$api.postForward(this.form).then(res => {
            this.ruleId = res.data
            this.$emit('createForm', this.ruleId)
          })
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.steps-titles {
  font-size: 14px;
  color: #575d6c;
  margin-bottom: 20px;
}
.steps-info {
  display: flex;
  justify-content: space-between;
  .steps-border {
    flex: 1;
  }
  .steps-right {
    flex: 1;
    margin-left: 26px;
  }
}
</style>



